import React, { Component } from 'react';
import translations from '../translations.json';
import p1 from '../img/plans/plan-4.jpg';
import p2 from '../img/plans/plan-5.jpg';
import p3 from '../img/plans/plan-6.jpg';

class HomePage extends Component {
  render () {

    return (
      <div>
        <h1>{translations['prebuild']['title'][this.props.langValue]}</h1>
        <p dangerouslySetInnerHTML={{ __html: translations['prebuild']['description'][this.props.langValue] }}></p>
        <div className="row d-block">
          <a href={p1} data-lightbox="plans" data-title="">
            <img className="img-fluid col-4  pb-3" src={p1} alt="" />
          </a>
          <a href={p2} data-lightbox="plans" data-title="">
            <img className="img-fluid col-4  pb-3" src={p2} alt="" />
          </a>
          <a href={p3} data-lightbox="plans" data-title="">
            <img className="img-fluid col-4  pb-3" src={p3} alt="" />
          </a>
        </div>
      </div>
    );
  }
}

export default HomePage;



