import React, { Component } from 'react';
import translations from '../translations.json';

class Schedule extends Component {
  render () {

    return (
			<div>
        <h1>{translations['schedule']['title'][this.props.langValue]}</h1>
        <p>{translations['schedule']['description'][this.props.langValue]}</p>
			  <div className="card">
			    <div className="card-header" id="workshop">
			      <h5 className="mb-0">
								{translations['schedule']['w1'][this.props.langValue]}
			      </h5>
			    </div>
			    <div id="weekend1" className="collapse show" aria-labelledby="workshop">
			      <div className="card-body">
			        <p>Fr 14. Sep (15h00)- So 16. Sep (20h00)</p>
			        <p></p>
			      </div>
			    </div>
			  </div>
				<br></br>
			
			  <div className="card">
			    <div className="card-header" id="headingTwo">
			      <h5 className="mb-0">
								{translations['schedule']['w2'][this.props.langValue]}
			      </h5>
			    </div>
			    <div id="week1"  aria-labelledby="headingTwo">
			      <div className="card-body">
			        <p>Mo 17 Sep (09h00)- Fr 21 Sep (15h00)</p>
			        <p>Workshops, Seminare, Vortrge</p>
			        <p>Entwerfen von Tiny Houses In A Barn</p>
			        <p>Experimentelles Bauen mit rtlichen, alternativen und nachhaltigen Baumaterialien</p>
			      </div>
			    </div>
			  </div>
				<br></br>

			  <div className="card">
			    <div className="card-header" id="headingThree">
			      <h5 className="mb-0">
								{translations['schedule']['w3'][this.props.langValue]}
			      </h5>
			    </div>
			    <div id="collapseThree"  aria-labelledby="headingThree">
			      <div className="card-body">
			        <p>Fr 21 Sep (15h00)- So 23 Sep (20h00)</p>
			        <p>Live Musik, DJs, Organic Food, Cold n Hot Drinks, Art Installations, Funky Fashion</p>
			      </div>
			    </div>
			  </div>
        <br></br>
        <h3>{translations['schedule']['full'][this.props.langValue]}</h3>
        <iframe title="Full Schedule" id="schedule-iframe" src="https://docs.google.com/spreadsheets/d/e/2PACX-1vR0MgxeKkLDM5_OWw6VmeH5dogJ5ZXUfPb9IeRYJOIMV2J8unShrUVlKaSsMV8X3XEK39pvsCB_JSxg/pubhtml?gid=1565420220&amp;single=true&amp;widget=true&amp;headers=false"></iframe>
			</div>
    );
  }
}
export default Schedule;



