import React, { Component } from 'react';
import mh from '../img/frontPage/mh.jpg';
import pano from '../img/frontPage/yellow_pano.jpg';
import music from '../img/frontPage/music.jpg';
import wall from '../img/frontPage/wall.jpg';
import shack from '../img/frontPage/shack.jpg';
// import  from '../img/.jpg';
import translations from '../translations.json';

class HomePage extends Component {
  render () {
		// <div className="background-field">
    return (
      <div className="container">
				<div>
        	<h1 className="title-text">{translations['homepage']['title'][this.props.langValue]}</h1>
        	<h3 className="title-text">@ <a href="https://www.facebook.com/muellerhof19/">MUELLERHOF19</a></h3>
        	<h3>{translations['homepage']['dates'][this.props.langValue]} 21-23</h3>
					<div className="d-none col-12">
						<img src={pano} className="img-fluid" alt="fielf" />
        	</div>
				</div>
				<div className="row">
					<div className="col-md-7 col-12" dangerouslySetInnerHTML={{__html: translations['homepage']['intro'][this.props.langValue]}}></div>
					<div className="col-md-5 col-12">
          	<a href={mh} data-lightbox="mhspace" data-title="">
          	  <img className="img-fluid" src={mh} alt="" />
          	</a>
        	</div>
        </div>
				<hr></hr>
				<div className="row">
					<div className="col-md-5 col-12">
          	<a href={wall} data-lightbox="mhspace" data-title="">
          	  <img className="img-fluid pb-3" src={wall} alt="" />
          	</a>
          	<a href={shack} data-lightbox="mhspace" data-title="">
          	  <img className="img-fluid pb-3" src={shack} alt="" />
          	</a>
          	<a href={music} data-lightbox="mhspace" data-title="">
          	  <img className="img-fluid pb-3" src={music} alt="" />
          	</a>
        	</div>
					<div className="col-md-7 col-12" dangerouslySetInnerHTML={{__html: translations['homepage']['content'][this.props.langValue]}}></div>
        </div>
      </div>
    );
  }
}

export default HomePage;



